import React,{useState} from 'react';
import Email from "emailjs-com";

function Contacts(){
    const [name,setName]=useState("");
    const [email, setEmail]=useState("");
    const [phone, setPhone]=useState(1);
    const [subject, setSubject]=useState("");
    const [msg, setMsg]=useState("");
    
    const handleSetName=(e)=>{
        setName(e.target.value);
    }
    const handleSetEmail=(e)=>{
        setEmail(e.target.value)
    }
    const handleSetPhone=(e)=>{
        setPhone(e.target.value)
    }
    const handleSetSubject=(e)=>{
        setSubject(e.target.value)
    }
    const handleSetMsg=(e)=>{
        setMsg(e.target.value);
    }
    function sendEmail(e){
        e.preventDefault();
        if(name!=="" && email!=="")
        Email.sendForm('service_khhx9v4', 'template_svwgasd', e.target, 'user_PXEv75bz6CKc4elCLMcog')
      .then((result) => {
          console.log(result.text);
      }, (error) => {
          console.log(error.text);
      });
      e.target.reset();
    }

    return(
        <div className="contacts"> 
            <h1>Contacts</h1>
            <div className="contact">
                <h3>Email form</h3>
                <form onSubmit={sendEmail}>
                    <div className="textFields">
                        <label htmlFor="name"><span>Name: </span></label>
                        <input type="text" id="name" name="name" value={name} onChange={handleSetName}/>
                        <label htmlFor="email"><span>Email: </span></label>
                        <input type="email" id="email" name="email" value={email} onChange={handleSetEmail} />
                        <label htmlFor="subject"><span>Subject: </span></label>
                        <input type="text" id="subject" name="subject" value={subject} onChange={handleSetSubject}/>
                        <label htmlFor="phone"><span>Phone: </span></label>
                        <input type="tel" id="phone" name="phone" value={phone} onChange={handleSetPhone}/>
                    </div>
                    <div className="textArea">
                        <label htmlFor="textArea"><span>Message:</span></label>
                        <textarea id="textArea" name="message" value={msg} onChange={handleSetMsg}/>
                    </div>
                    <div className="sendForm">
                        <input type="submit" value="Send Email"/> 
                    </div>
                </form>
            </div>
        </div>
    );
}
export default Contacts;