import React from 'react';
import HTMLlogo from '../Images/htmlLogo.png'
import ProgLogo from '../Images/programming.png'
import '../Styles/Components.css'

export default function Skills(){
    return(
        <div className='skills'>
            <h1>Skills</h1>
            <div className="wrap">
                <section>
                    <div className="head">
                        <img src={HTMLlogo} alt='html_logo' className="icon"/>
                        <p><span>Front-end Developer</span></p>
                    </div>
                    <ul>
                        <li>HTML5</li>
                        <li>CSS</li>
                        <li>JavaScript</li>
                        <li>SASS</li>
                        <li>ReactJS</li>
                        <li>GitHub</li>
                    </ul>
                </section>
                <section>
                    <div class="head">
                        <img src={ProgLogo} alt="econo" class="icon"/>
                        <p><span>Software Developer</span></p>
                    </div>
                    <ul>
                        <li>C/C#</li>
                        <li>Java</li>
                        <li>SQL</li>
                        <li>Python</li>
                        <li>Kotlin</li>
                        <li>Assembly</li>
                        <li>GitHub</li>
                    </ul>
                </section>
            </div>
        </div>
    );
}