import React from 'react';
import '../Styles/Components.css'


export default function About(){
    return(
        <div className="about">
            <h1>About me</h1>
            <p>Hi! I am Roman, and this is my portfolio. Here you can find some information about me, my education and my skills.</p>
            <p>
                I had graduated from Tel Hai Academic College, Israel in 2009 as a Software Developer. After that, I worked as a database 
                administrator for a small company where I managed a local database for a company warehouse. Beside that, 
                I worked as freelancer and did projects for clients. In 2017, I got Bachelor of Arts degree in Economics in Bar Ilan 
                University, Israel, and after that I moved to Canada.
            </p>
            <p>
                I enjoy to be a team player. The organization of the workplace and workflow play an important role for me. Over the years of 
                working in freelance, I have learned to work with people, to understand their needs, and to help them to solve problems. 
                Now I decided to change directions and do web development. I love to create something new, to give it form and functionality.
            </p>
        </div>
    );
}