import React from 'react';
import {Link} from 'react-router-dom';

export default function Navigation(){
    return(
        <nav>
            <Link to='/'>Home</Link>
            <Link to='/About' className='navg'>About</Link>
            <Link to='/Skills' className='navg'>Skills</Link>
            <Link to='/Education' className='navg'>Education</Link>
            <Link to='/Contacts' className="navg">Contacts</Link>
        </nav>
    );
}
